import { useState } from 'react'
import useLocalStorage from './useLocalStorage'
import 'bootstrap/dist/css/bootstrap.min.css'
import Timer from './Timer'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import ShowCubeModal from './ShowCube'
import { getRandomMoves, addRecord } from './utils'
import RecordList from './RecordList'
import DisplayStats from './DisplayStats'
import './App.scss'
import RenderFace from './RenderFace'
import { generateCube } from './cube-utils'

function ModeChooser({ setMode, show, handleClose }) {
  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Choose Mode</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="d-flex justify-content-center">
          <button
            className="btn btn-primary m-2"
            onClick={() => {
              setMode('normal')
              handleClose()
            }}
          >
            Normal
          </button>
          <button
            className="btn btn-primary m-2"
            onClick={() => {
              setMode('f2l')
              handleClose()
            }}
          >
            F2L
          </button>
        </div>
      </Modal.Body>
    </Modal>
  )
}

function App() {
  const [scramble, setScramble] = useState([])
  const [timerIsActive, setTimerIsActive] = useState(false)

  const [showStats, setShowStats] = useState(false)
  const [showCube, setShowCube] = useState(false)
  const [showModeChooser, setShowModeChooser] = useState(false)

  const [mode, setMode] = useState('normal')

  const [records, setRecords] = useLocalStorage('records', {})

  const [currentTimeSaved, setCurrentTimeSaved] = useState(false)

  const [cube, setCube] = useState(generateCube())

  const numMoves = mode === 'normal' ? 18 : 10

  return (
    <div
      style={{
        minHeight: '100vh',
      }}
      className="bg-light m-0"
    >
      <div className="container bg-white p-3">
        <h1 className="text-center">Rubik's Timer</h1>
        <ShowCubeModal
          cube={cube}
          setCube={setCube}
          scramble={scramble}
          show={showCube}
          handleClose={() => setShowCube(false)}
        />
        <p className="text-center">
          To perform a scramble, hold the solved cube with the yellow side facing up and the blue
          side facing you (the F side)
        </p>
        <div>
          <p
            style={{
              backgroundColor: '#0d6efd',
            }}
            className="text-center my-4 fs-2 text-white move-display"
          >
            {scramble.map((move, index) => (
              <span className="p-2" key={index}>
                {move}{' '}
              </span>
            ))}
          </p>
        </div>

        <div className="text-center my-4">
          <button
            className="btn btn-primary"
            onClick={() => {
              if (timerIsActive) return
              const moves = getRandomMoves(numMoves)
              setScramble(moves)
            }}
            disabled={timerIsActive}
          >
            Generate New Scramble
          </button>
        </div>
        <Timer
          isActive={timerIsActive}
          setIsActive={setTimerIsActive}
          onStart={() => {
            setCurrentTimeSaved(false)
          }}
          onSave={(elapsedTime) => {
            if (scramble.length === 0 || elapsedTime === 0) {
              return
            }
            if (currentTimeSaved) {
              return
            }
            const newRecords = addRecord(records, scramble, elapsedTime, { mode })
            setRecords(newRecords)
            setCurrentTimeSaved(true)
          }}
        />

        {!timerIsActive && (
          <>
            <DisplayStats
              records={records}
              handleClose={() => setShowStats(false)}
              show={showStats}
            />

            <h2 className="my-4 text-center">Existing Records</h2>
            <RecordList
              records={records}
              setRecords={setRecords}
              setScramble={setScramble}
              currentScramble={scramble}
            />
            <div className="text-center my-4 d-flex flex-row justify-content-around">
              <Button className="" variant="primary" onClick={() => setShowModeChooser(true)}>
                {mode === 'normal' ? 'Normal' : 'F2L'}
              </Button>
              <button className="btn btn-primary" onClick={() => setShowCube(!showCube)}>
                Show Cube
              </button>
              <Button variant="primary" onClick={() => setShowStats(true)}>
                Show Stats
              </Button>
              <button
                className="btn btn-danger"
                onClick={() => {
                  if (window.confirm('Are you sure you want to clear all records?')) {
                    setRecords({})
                  }
                }}
              >
                Clear Records
              </button>
            </div>
            <div className="text-center row justify-content-center my-4">
              <RenderFace face={cube.F} />
            </div>
          </>
        )}
      </div>
      <ModeChooser
        show={showModeChooser}
        handleClose={() => setShowModeChooser(false)}
        setMode={setMode}
      />
    </div>
  )
}

export default App
