import { useState, useEffect } from 'react';
import { generateCube, performMove } from './cube-utils'
import { getOppositeMove } from './utils'
import Modal from 'react-bootstrap/Modal'
import ShowFace from './RenderFace';

function ShowCubeModal({ show, handleClose, scramble, cube, setCube }) {
  // const [cube, setCube] = useState(generateCube())
  const [moveIndex, setMoveIndex] = useState(0)

  const basicMoves = ['L', 'F', 'R', 'B', 'U', 'D']

  useEffect(() => {
    let newCube = generateCube()

    // Since performMove is synchronous, moves will run in order
    scramble.forEach((move) => {
      newCube = performMove(move, newCube)
    })
    setMoveIndex(scramble.length)
    setCube(newCube)
  }, [scramble])

  useEffect(() => {
    // listen for keyboard forward and back arrows
    const handleKeyDown = (e) => {
      if (e.key === 'ArrowLeft') {
        if (moveIndex > 0) {
          setMoveIndex(moveIndex - 1)
          setCube(performMove(getOppositeMove(scramble[moveIndex - 1]), cube))
        }
      } else if (e.key === 'ArrowRight') {
        if (moveIndex < scramble.length) {
          setCube(performMove(scramble[moveIndex], cube))
          setMoveIndex(moveIndex + 1)
        }
      }
    }
    window.addEventListener('keydown', handleKeyDown)

    return () => {
      window.removeEventListener('keydown', handleKeyDown)
    }
  })

  return (
    <Modal size="xl" show={show} onHide={handleClose}>
      <Modal.Body>
        <div className="container">
          <div>
            <p
              style={{
                backgroundColor: '#0d6efd',
              }}
              className="text-center my-4 fs-2 text-white move-display"
            >
              {scramble.map((move, index) => (
                <span className="p-2" key={index}>
                  {move}{' '}
                </span>
              ))}
            </p>
          </div>

          <div className="row">
            <div className="col-3">.</div>

            {['U'].map((faceSymbol, i) => {
              const face = cube[faceSymbol]
              return <ShowFace face={face} key={i} />
            })}
          </div>
          <div className="row">
            {['L', 'F', 'R', 'B'].map((faceSymbol, i) => {
              const face = cube[faceSymbol]
              return <ShowFace face={face} key={i} />
            })}
          </div>
          <div className="text-center row">
            <div className="col-3">.</div>

            {['D'].map((faceSymbol, i) => {
              const face = cube[faceSymbol]
              return <ShowFace face={face} key={i} />
            })}
          </div>
          <hr />
          <div className="row">
            {basicMoves.map((move, i) => {
              return (
                <div key={`move-button-${move}`} className="col-2 text-center">
                  <button
                    className="btn btn-primary w-50"
                    onClick={() => setCube(performMove(move, cube))}
                  >
                    {move}
                  </button>
                </div>
              )
            })}
          </div>
          <div className="row mt-2">
            {basicMoves
              .map((m) => `${m}'`)
              .map((move, i) => {
                return (
                  <div key={`prime-move-button-${i}`} className="col-2 text-center">
                    <button
                      className="btn btn-primary w-50"
                      onClick={() => setCube(performMove(move, cube))}
                    >
                      {move}
                    </button>
                  </div>
                )
              })}
            <div className="col-2 mt-3">
              <button
                className="btn btn-primary"
                onClick={() => {
                  setCube(generateCube())
                  setMoveIndex(0)
                }}
              >
                Reset
              </button>
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  )
}

export default ShowCubeModal
